            p14: 'La Casa de Propuestas UMA fue creada por Matas para ayudar a la expansin, incentivando a los constructores y creadores a trabajar en y con UMA.',
            p15: 'Las rondas se realizan semanalmente y todas las propuestas ganadoras son recompensadas con un UMA NFT cada una, que se convierten, adems, en un voto para todas las rondas futuras.',

            h16: 'Cmo puedo hacer el mint de un UMA? <br/>No puedo hacer el mint, cmo me inscribo en una lista de espera?',
            p17: 'Las rondas se celebran semanalmente en la  Casa de Propuestas UMA, que es el tiempo donde se presentan propuestas. Las propuestas son tus ideas y visin de cmo ampliaras el UMA-verso.',
            p18: 'Al final de una ronda, los titulares de UMAs pueden votar las 5 mejores propuestas. Las 5 mejores propuestas podrn entonces hacer el mint (acuar) su NFT de UMA, lo que les permitir votar en futuras rondas de la  Casa de Propuestas.',
            p19: 'As es como el UMA-verso se expande y evoluciona con el tiempo a travs de la creatividad de las propuestas de los contribuyentes.',

