            p22: 'En caso de empate, tienen prioridad las propuestas presentadas antes. Por lo tanto, ganan las 5 primeras propuestas que aparecen en Prop House despus de una ronda completa.<br/>1 UMA = 1 voto.',
            p23: 'Si no lo consigues en una ronda determinada, vuelve a presentar tu propuesta.<br />Volver a presentarla es fcil y es lo recomendable.',

            h24: 'Cul es la mejor manera de participar?',
