            h26: 'Cuntas UMA habr?',
            p27: 'Esperamos que muchas. Los UMAs estn pensadas para ser inflacionarias, donde el valor potencial perdido por la inflacin ser ms que compensado por las nuevas contribuciones aadidas al ecosistema.',
            p28: '<a href=\"https://twitter.com/matascup\" target=\"_blank\" rel=\"nofollow\">Matas</a> se reserva el 10% del suministro total.',

