            p9: "Une organisation autonome dcentralise (DAO) est un systme dvelopp pour distribuer la prise de dcision, la gestion et la proprit de l'entit.",
            p10: "Au lieu de s'appuyer sur un individu ou un groupe d'individus pour orienter la direction de l'entit, une DAO donne  chaque membre une voix, un vote et la possibilit de proposer des initiatives.",
            p11: "Pour UMA DAO, cela se fait via la UMA House sur l'infrastructure publique Prop House.",
          
            h12: "Qu'est-ce que UMA DAO/UMA House?",
