            p14: "La UMA House a t cre par <a href=\"https://twitter.com/matascup\" target=\"_blank\" rel=\"nofollow\">Matas</a> pour aider  l'expansion, en incitant constructeurs et crateurs  travailler sur et avec UMA.",
            p15: "Des tours sont organiss chaque semaine et toutes les propositions gagnantes sont rcompenses par une NFT UMA qui sert de vote pour tous les prochains tours.",
          
            h16: "Comment puis-je minter un UMA? Je ne peux pas minter! <br /> Comment puis-je tre ajout  la liste d'attente?",
            p17: "Des tours ont lieu chaque semaine  la UMA House, c'est votre opportunit pour soumettre une proposition. Les propositions sont votre chance de prsenter votre vision de la manire dont vous tendriez l'univers UMA.",
            p18: " la fin d'un tour, les membres de la DAO peuvent voter pour les 5 meilleures propositions. Ces 5 meilleures propositions peuvent ensuite minter leur NFT UMA, ce qui leur permet ensuite de voter lors des prochains tours de la UMA House.",
            p19: "C'est ainsi que l'univers UMA s'tend et volue au fil du temps grce  la crativit des propositions des contributeurs.",
          
